.form-field {
  box-sizing: border-box;
  border: 1px solid #828282;
  display: block;
  width: 100%;
  padding: 12px 20px;
  height: 48px;
  color: #333333;

  &:hover {
    border: 1px solid #333;
  }
  
  &:focus {
    border: 1px solid #0C797A;
    outline: none;
  }

  &::placeholder {
    font-size: 16px;
    line-height: 24px;
    color: #BDBDBD;
  }

  &--textarea {
    font-family: inherit;
    height: 128px;
    resize: none;
  }

  &__social-media {
    display: flex;
  }
}