.nav {
  margin-bottom: 66px;
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: #f7f7f7;
  opacity: 0.8;
  border-bottom: 0.5 solid rgb(130, 130, 130);

  
  @include onMobile {
    display: none;
  }
  
  @include onTablet {
    display: none;
  }

  &__items {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0;
    height: 76px;
  }

  &__item {
    list-style-type: none;
    
    &:not(:last-child) {
      margin-right: 80px;
    }
  }

  &__link {
    text-decoration: none;
    color: #333;
    font-size: 14px;
    line-height: 20px;
    transition: $transition-duration;

    &:hover {
      font-weight: 700;
      color: #131313;
    }
  }
}
