.button {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  transition: background-color $transition-duration,
    transform $transition-duration;
  
  &:active {
    transform: scale(0.9);
  }

  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  
  width: 102px;
  height: 48px;
  border: none;

  &--buy {
    background-color: #333;
    padding: 0;

    &:hover {
      background-color: #131313;
    }

    &:disabled {
      background-color: #d0d0d0;
    }

    @include onTablet {
      margin-left: 30px;
    }

    @include onDesktop {
      margin-left: 47px;
    }
  }

  &--send {
    background-color: #39BEBF;

    &:hover {
      background-color: #0C797A;
    }
  }
}
